import Vue from 'vue';
import Router from './Router';
import {Store} from './Store';
import VueCookie from 'vue-cookie';
import Validate from 'vue-vd-validate';
import Loader from './components/Loader';
import DatePicker from 'vue2-datepicker';
import Sidebar from './components/Sidebar';
import {beforeRoute} from './middlewares/AuthMiddleware';
import {UserClient} from '@/api-clients/UserClient';

/** ---- Start {import styles and jquery packages} ---- **/
	import './assets/css/theme.css';
	import './assets/css/custombox.min.css';
/** ---- End {import styles and jquery packages} ---- **/

Vue.use(VueCookie);
Vue.use(DatePicker);
Vue.use(Validate, {
	rules: {
		date: (value) => {
			let error = false;
			if (!Date.parse(value)) {
				error = 'A valid date is required.';
			}

			return error;
		}
	},
	customClass: 'v-error',
	errors: {
		required: 'This field is required',
		number: 'A well formed number is expected'
	}
});

window.jQuery = window.$ = require('jquery');

new Vue({
	el: '.app-zcirfeu52g',
	store: Store,
	router: Router,
	components: {
		Loader,
		Sidebar
	},
	mounted() {
		UserClient.get().then((response) => {
			Store.dispatch('auth/updateAuthenticatedStatus', true);
			Store.dispatch('auth/updateHandshakeStatus', true);
		}).catch((error) => {
			// Set to true since the app has no authentication implementation.
			Store.dispatch('auth/updateAuthenticatedStatus', true);
			Store.dispatch('auth/updateHandshakeStatus', true);
		});
	}
});


