import {ApiClientFactory} from '@/ApiClientFactory';
import {TRAVEL_PORTAL_API_URL} from '@/Config';

let clientFactory = new ApiClientFactory(TRAVEL_PORTAL_API_URL);

export const SearchClient = {

	/**
	* Returns flights that match the provided search criterias.
	*
	* param {data}
	*/
	getFlights(data) {
		return clientFactory.post('flight/search-flight', data);
	},

	/**
	* Returns airports that match the types string.
	*
	* param {data}
	*/
	searchTypedAirport(criteria = '') {
		return clientFactory.get('plugins/airlines-type-ahead/' + criteria);
	},

	/**
	* Requests access token with the provided credentials.
	*
	* param {data}
	*/
	requestToken(data) {
		return clientFactory.post('auth/login', data);
	}

}


