<template>
	<form class='container space-2 overflow-auto' @submit.prevent='getFlights'>

		<SearchResults :flights='flights'></SearchResults>

		<main id='content' role='main'>

			<div class='bg-light p-4 mb-5'>
				<div class='alert alert-danger mb-7' v-if='search.done && !search.successful' role='alert'>
					<h4 class='alert-heading'>
						No flights found :(
					</h4>
					<p class='alert-text mb-0'>
						Try changing your search parameters.
					</p>
				</div>

				<div class='row'>
					<div class='col-sm-6 mb-6'>
						<div class='js-form-message'>
							<label class='form-label'>
								Departure city
								<span class='text-danger'>*</span>
							</label>

							<div class='form-group'>
								<input lang='eng' type='text' class='form-control' v-model='payload.body.origin_destinations[0].departure_city' v-validate="'required'" data-vd-name='departure_city' :disabled='searchConfig.departure.disabled' @keyup="searchTypedAirport($event, 'departure', 'destination')">
								<small class='form-text text-muted'>
									Start typing to search for departure city
								</small>
								<AutoComplete :active.sync='autoCompleteConfig.departure.active' :items='autoCompleteConfig.departure.items' :onItemSelected='onCitySearchItemSelected' attribute='departure_city'></AutoComplete>
							</div>
						</div>
					</div>

					<div class='col-sm-6 mb-6'>
						<div class='js-form-message'>
							<label class='form-label'>
								Destination city
								<span class='text-danger'>*</span>
							</label>

							<div class="form-group">
								<input type='text' class='form-control' v-model='payload.body.origin_destinations[0].destination_city' v-validate="'required'" data-vd-name='destination_city' :disabled='searchConfig.destination.disabled' @keyup="searchTypedAirport($event, 'destination', 'departure')">
								<small class='form-text text-muted'>
									Start typing to search for destination city
								</small>
								<AutoComplete :active.sync='autoCompleteConfig.destination.active' :items='autoCompleteConfig.destination.items' attribute='destination_city' :onItemSelected='onCitySearchItemSelected'></AutoComplete>
							</div>
						</div>
					</div>

					<div class='col-sm-6 mb-6'>
						<div class='js-form-message'>
							<label class='form-label'>
								Departure date
								<span class='text-danger'>*</span>
							</label>

							<div class='form-group'>
								<date-picker class='form-control' v-model='payload.body.origin_destinations[0].departure_date'confirm lang='eng' @confirm='pushDepartureDateToFormParams' v-validate="'required|date'" data-vd-name='departure_date' :not-before='datesConfig.departure.disabled[0]'></date-picker>
							</div>
						</div>
					</div>

					<div class='col-sm-6 mb-6'>
						<div class='js-form-message'>
							<label class='form-label'>
								Return date
								<span class='text-danger'>*</span>
							</label>

							<div class="form-group">
								<date-picker class='form-control' v-model='payload.body.origin_destinations[0].return_date'confirm lang='eng' @confirm='pushReturnDateToFormParams' v-validate="'required|date'" data-vd-name='return_date' :not-before='datesConfig.return.disabled[0]'></date-picker>
							</div>
						</div>
					</div>

					<div class='col-sm-6 mb-6'>
						<div class='js-form-message'>
							<label class='form-label'>
								Cabin class
								<span class='text-danger'>*</span>
							</label>
							<div class='form-group'>
								<select class='form-control custom-select' v-model='payload.body.search_param.cabin' v-validate="'required'" data-vd-name='cabin' data-vd-delay='500'>
									<option value='All' selected>
										All
									</option>
									<option value='First'>
										First
									</option>
									<option value='Economy'>
										Economy
									</option>
									<option value='Premium'>
										Premium
									</option>
									<option value='Business'>
										Business
									</option>
								</select>
							</div>
						</div>
					</div>

					<div class='col-sm-6 mb-6'>
						<div class='js-form-message'>
							<label class='form-label'>
								Number of infants
								<small class='text-muted'>
									(0 - 2 yo)
								</small>
								<span class='text-danger'>*</span>
							</label>

							<div class="form-group">
								<input type='text' class='form-control' v-model='payload.body.search_param.no_of_infant' v-validate="'required|number'" data-vd-name='no_of_infant'>
							</div>
						</div>
					</div>

					<div class='col-sm-6 mb-6'>
						<div class='js-form-message'>
							<label class='form-label'>
								Number of children
								<small class='text-muted'>
									(2 - 12 yo)
								</small>
								<span class='text-danger'>*</span>
							</label>

							<div class="form-group">
								<input type='text' class='form-control' v-model='payload.body.search_param.no_of_child' v-validate="'required|number'" data-vd-name='no_of_child'>
							</div>
						</div>
					</div>

					<div class='col-sm-6 mb-6'>
						<div class='js-form-message'>
							<label class='form-label'>
								Number of adults
								<small class='text-muted'>
									(> 12 yo)
								</small>
								<span class='text-danger'>*</span>
							</label>

							<div class="form-group">
								<input type='text' class='form-control' v-model='payload.body.search_param.no_of_adult' v-validate="'required|number'" data-vd-name='no_of_adult'>
							</div>
						</div>
					</div>

					<div class='col-sm-12'>
						<button class='btn btn-primary' type='submit'>
							Get flights
						</button>
					</div>
				</div>
			</div>
		</main>
	</form>
</template>

<script>
	import Custombox from '@/plugins/Custombox';
	import {SearchClient} from '@/api-clients/SearchClient';
	import AutoComplete from '@/components/search/AutoComplete';
	import SearchResults from '@/views/partials/home/SearchResults';
	import {
		TRAVEL_PORTAL_API_USERNAME,
		TRAVEL_PORTAL_API_PASSWORD
	} from '@/Config';

	export default {
		components: {
			SearchResults,
			AutoComplete
		},

		data () {
			return {
				searchTimer: null,

				autoCompleteConfig: {
					departure: {
						active: false,
						items: []
					},

					destination: {
						active: false,
						items: []
					}
				},

				searchConfig: {
					departure: {
						disabled: false
					},

					destination: {
						disabled: false
					}
				},

				datesConfig: {
					departure: {
						formatted: null,
						disabled: [new Date(), new Date()]
					},

					return: {
						formatted: null,
						disabled: [new Date(), new Date()]
					}
				},
				search: {
					done: false,
					successful: false
				},
				payload: {
					body: {
						origin_destinations: [
							{
								departure_city: null,
								destination_city: null,
								departure_date: null,
								return_date: null,
							}
						],
						search_param: {
							no_of_adult: 1,
							no_of_child: 1,
							no_of_infant: 0,
							cabin: 'All'
						}
					}
				},
				flights: []
			}
		},

		methods: {
			getFlights() {
				this.$validator.validate().then(result => {
					if (result.valid) {
						this.$store.dispatch('process/dispatchProcess', {
							processId: 'PROCESS.GET_FLIGHTS',
							action: (reset) => {
								SearchClient.getFlights(this.payload).then((response) => {
									this.search.done = true;
									this.search.successful = true;
									console.log(response.data);
									this.resolveSearchResultModal();
									reset();
								}).catch((err) => {
									this.search.done = true;
									this.search.successful = false;

									// Since there's no clear error response so as to know what type of error we received, we would assume this is a token error and try to retrieve the token again.
									this.requestAndSetToken().then(() => {
										this.$store.dispatch('process/reworkProcess', {
											time: 2,
											retry: true,
											processId: 'PROCESS.GET_FLIGHTS'
										});
									});
								});
							},
							debug: true
						});
					}
				});
			},

			requestAndSetToken() {
				let credentials = {
					body: {
						email: TRAVEL_PORTAL_API_USERNAME,
						password: TRAVEL_PORTAL_API_PASSWORD
					}
				};

				return SearchClient.requestToken(credentials).then((response) => {
					this.$cookie.set(
						'token',
						response.data.body.data.api_token
					);
				});
			},

			pushDepartureDateToFormParams() {
				if (this.payload.body.origin_destinations[0].departure_date) {
					let date = new Date(this.payload.body.origin_destinations[0].departure_date);

					this.payload.body.origin_destinations[0].departure_date = this.formatDate(date, '/');
					this.datesConfig.return.disabled[0] = new Date(this.formatDate(date, '/'));
				}
			},

			pushReturnDateToFormParams() {
				if (!this.payload.body.origin_destinations[0].departure_date) {
					return this.payload.body.origin_destinations[0].return_date = null;
				}

				if (this.payload.body.origin_destinations[0].return_date != null) {
					let date = new Date(this.payload.body.origin_destinations[0].departure_date);
					this.payload.body.origin_destinations[0].return_date = this.formatDate(date, '/');
				}
			},

			formatDate(date, separator = '-') {
				date = new Date(date);
				return date.getFullYear()
				+ separator +
				(date.getMonth() + 1)
				+ separator +
				date.getDate();
			},

			searchTypedAirport(e, activeSearchType, disabledSearchType) {
				if (e.target.value.length < 1) {
					this.searchConfig[disabledSearchType].disabled = false;
					return this.autoCompleteConfig[activeSearchType].active = false;
				}

				this.searchConfig[disabledSearchType].disabled = true;
				clearTimeout(this.searchTimer);
				this.searchTimer = setTimeout(() => {
					SearchClient.searchTypedAirport(e.target.value).then((response) => {
						this.autoCompleteConfig[activeSearchType].active = true;
						this.autoCompleteConfig[activeSearchType].items = response.data.data;
					});
				}, 1000);
			},

			onCitySearchItemSelected(item, attribute) {
				this.payload.body.origin_destinations[0][attribute] = item.code;
				this.autoCompleteConfig.departure.active = false;
				this.autoCompleteConfig.destination.active = false;
				this.searchConfig.departure.disabled = false;
				this.searchConfig.destination.disabled = false;
			},

			resolveSearchResultModal() {
				new Custombox.modal({
					content: {
						target: '#search-results'
					}
				}).open();
			}
		}
	}
</script>


