<style lang='scss' scoped>
	.autocomplete {
		width: 100%;
		height: 150px;
		max-height: 200px;
		box-shadow: 0px 2px 4px #ddd;
		border-radius: 5px;
		position: absolute;
		overflow-y: auto;
		left: 0;
		z-index: 3;
		background: #fff;
		padding: 15px;
	}
</style>

<template>
	<div class='autocomplete' v-if='active'>
		<ul class='list-group'>
			<li v-for='(item, index) in items' :key="'item-' + index" class='list-group-item'>
				<a href='javascript:;' @click='resolveSelectedItem(item)'>
					{{ item.name }}
				</a>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		props: {
			attribute: {
				type: [String, Number],
				default: () => {
					return null;
				}
			},

			active: {
				type: Boolean,
				default: false
			},

			items: {
				type: Array,
				default: () => {
					return [];
				}
			},

			onItemSelected: {
				type: Function,
				default: () => {
					return null;
				}
			}
		},

		data() {
			return {

			}
		},

		methods: {
			resolveSelectedItem(item) {
				if (typeof this.onItemSelected == 'function') {
					this.onItemSelected(item, this.attribute);
				}
			}
		}
	}
</script>


